import eventHub from '../assets/eventHub' 
const FastClick = require('fastclick')
FastClick.attach(document.body);
export default {
    name:'recordIndex',
    props:['message'],
    data(){
        return {
            msg:'默认值'
        }
    },
    created(){
        console.log(111);
    },
    methods:{
        sendParentMsg:function(){
            this.$emit('listenToChild','我这里是子组件发出的信息')
        }
    },
    mounted(){
        let _this = this;
        eventHub.$on('username',(msg) => {
            console.log('msg',msg);
            _this.msg = msg;
        })
    }
}



